<template>
        <div>
                <div class="search">
                        <input v-model="keyword" class="inp" type="text" placeholder="请您输入要搜索的内容">
                </div>     
                <div class="search-content" ref="wrapper" v-show="keyword">
                        <ul>
                                <li class="item" v-for="(item,index) of list" :key="index"
                                @click= "clickItem(item.name)">{{item.name}}</li>
                                <li class="item" v-show= "hasNoData">没有找到匹配的数据！</li>
                        </ul>
                </div> 
        </div>
 </template>

<script>
import Bscroll from "better-scroll"
export default{
        name : "citySearch",
        props : {
                cities : Object
        },
        data(){
                return {
                        keyword : "",
                        list : [],
                        timer : null,
                }
        },
        computed:{
                hasNoData(){
                        return !this.list.length
                }
        },
        methods:{
                clickItem( city ){
                        this.$store.dispatch('changeCity',city)
                        this.$router.push('/')
                        // console.log(this.$router)
                }
        },
        mounted(){
                this.scroll = new Bscroll(this.$refs.wrapper)
        },
        watch : {
                keyword (){
                        // 节流函数：当我们搜索地点的时候，中间间隔是300毫秒
                        if(this.timer){
                                clearTimeout(this.timer)
                        }
                        if( !this.keyword ){
                                this.list = []
                                return
                        }
                        this.timer = setTimeout(()=>{
                                const result = []
                                for( let i in this.cities ){
                                        this.cities[i].forEach((value)=>{
                                                if( value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1 ){
                                                        result.push(value)
                                                }
                                        })
                                }
                                this.list = result
                        },100)
                }
        }
}
</script>
<style lang="stylus" scoped>
@import '../../../assets/styles/varibles.styl';
.search
        height : .5rem
        background : $bgColor
        padding : .2rem
        .inp
                width  : 100%
                height : .5rem
                line-height : .5rem
                text-align : center 
                border-radius : .05rem
                color : #666
.search-content
       overflow : hidden
       z-index : 1
       position : absolute
       top :  1.75rem
       left : 0
       right : 0
       bottom : 0
       background : #ccc
       .item
                line-height: 0.7rem;
                padding-left: 0.2rem;
                border-bottom: 0.01rem #eee solid;
                color : #666
                background : #fff

</style>




