<template>
        <div class="icons">
                <swiper :options="swiperOption">
                        <swiper-slide v-for="(page,index) of pages" :key="index">
                                <div class="icon" v-for="item of page" :key="item.id">
                                        <img :src='item.imgUrl' alt="" class="icon-img"/>
                                        <p class="title">{{ item.desc }}</p>
                                </div>
                        </swiper-slide>
                </swiper>
        </div>
</template>

<script>
export default{
        name : "myicons",
        props : {
                iconList : Array
        },
        data () {
                return{
                        // 下边小点
                        swiperOption : {
                                pagination : '.swiper-pagination',
                                loop : false ,//让我们这个轮播插件支持循环
                        }
                }
        },
        computed:{
                // 它返回的是一个二维数组，把一个一维的数组拆分正一个二维的数组，第一个数组里边有8个数据（也就是第一页要展示的数据），
                // 第二个数组里边有1个数据（第二页要展示的数据），然后页码和对应的数据项做一个关联
                pages(){
                        const pages = []
                        this.iconList.forEach((item,index) =>{
                                const page = Math.floor(index/8)
                                if( !pages[page] ){
                                        pages[page] = []
                                }
                                pages[page].push(item)
                        })
                        return pages
                }
        }
}
</script>

<style lang="stylus" scoped>
@import '../../../assets/styles/mixins.styl'
        .icons
                overflow : hidden
                width : 100%
                height : 0
                padding-bottom : 50%
                font-size : 0
                margin-top : .1rem
                .icon
                        position : relative
                        width : 25%
                        height : 0
                        padding-bottom : 25%
                        text-align : center
                        display : inline-block
                        .icon-img
                                display : block
                                margin : 0 auto 
                                padding : .1rem 0 .05rem
                                width : 65% 
                                border-radius : .4rem 
                        .title
                                color : #000
                                font-size : .28rem
                                padding-top : .1rem
                                ellipsis()

                                
</style>


