import Vue from 'vue'
import Vuex from 'vuex'

// vue中用插件都是使用vue。use来使用的
Vue.use(Vuex)
let defaultCity = '上海'
try {
        if(localStorage.city){
                defaultCity = localStorage.city
        }
}catch(e){}

export default new Vuex.Store({
        state : {
                city : defaultCity
        },
        actions: {
                // 第一个参数是：上下文，第二个是：传过来的city
                changeCity(ctx,city){
                        // console.log(ctx,city)
                        // actions 执行 mutations 这个方法，要调用commit这个方法，正好ctx里边有这个方法
                        // 执行changeCity这个方法，传过去city这个值
                        ctx.commit('changeCity',city)
                }
        },
        mutations : {
                // 这里的state值的是公用的数据，我们让state.city = 传入进来的city
                changeCity (state , city){
                        // console.log(state)
                        state.city = city
                        try{
                                localStorage.city = city
                        }catch(e){}
                }
        }
})


