<template>
        <div>
                <city-header></city-header>
                <city-search :cities= "cities"></city-search>
                <city-list :gethotCity= "hotCity" :cities = "cities" :mydata= "mydata"></city-list>
                <alphabet :cities = "cities" @getNum="getData"></alphabet>
        </div>
</template>

<script>
import cityHeader from "./components/header"
import citySearch from "./components/city-search"
import cityList from "./components/citylist"
import alphabet from "./components/Alphabet"
import axios from "axios"
export default{
        name: 'city',
        components : {
                cityHeader,
                citySearch,
                cityList,
                alphabet,
         },
         data(){
                 return {
                         hotCity : [],
                         cities : {},
                         mydata : ""
                 }
         },
         methods:{
                 getCityInfo(){
                         axios.get('https://www.easy-mock.com/mock/5c2c8c6c1d8d3d65c3bf4e86/example/city')
                         .then(this.getInfoSucc)
                 },
                 getInfoSucc(res){
                         res = res.data
                         console.log(res)
                         if( res.ret && res.data ){
                                 this.hotCity = res.data.hotCities
                                 this.cities = res.data.cities
                                 console.log(this.cities)
                         }
                 },
                 getData(mydata){
                         this.mydata = mydata
                 },
         },
         mounted(){
                 this.getCityInfo()
         }
}

</script>

<style lang="stylus" scoped>


</style>


