<template>

        <div class="header">
                <div class="header-left">
                        <span class="iconfont">&#xe606;</span>
                </div>
                <div class="header-input">
                        <span class="iconfont">&#xe60b;</span>
                        请输入您的内容
                </div>
                <router-link to="/city">
                        <div class="header-right">
                                <!-- 为什么子组件都会用到$store呢，因为：在main.js中创建根实例的时候，把store传进去了，所以各个组件会用到store -->
                                {{ this.city }}
                                <span class="sj"></span>
                        </div>
                </router-link>
        </div>

</template>
<script>
import { mapState } from 'vuex'
export default {
        // 首页的名字
        name: "home",
        computed: {
                //   展开运算符：
                ...mapState(['city'])
        }
};
</script>

<style lang="stylus" scoped>
// 引入varibles.styl这个文件，里边的$bgColor这个变量,样式引入样式前面要加一个@
@import '../../../assets/styles/varibles.styl'

.header 
        display: flex
        height: 0.86rem
        // 使用$bgColor这个变量维护起来好维护
        background: $bgColor
        line-height: 0.86rem
        .header-left 
                width: 0.64rem
                float: left
                text-align: center
                color: #fff
        .header-input 
                flex: 1
                height: 0.62rem
                margin-top: 0.12rem
                margin-left: 0.2rem
                background: #fff
                border-radius: 0.1rem
                color: #ccc
                line-height: 0.62rem
                padding-left: 0.3rem
        .header-right 
                min-width: 1.04rem
                padding : 0 .2rem
                float: right
                text-align: center
                color: #fff
                .sj
                        width: 0
                        height: 0
                        border-left : 6px solid transparent
                        border-right : 6px solid transparent
                        border-top : 7px solid #fff
                        border-bottom : 0 solid transparent
                        border-radius : 1px
                        line-height : .86rem
                        display :inline-block
        

</style>


