<template>
          <div class="wrapper">
                <ul class="list">
                        <li class="item"
                                v-for="item of numbers"
                                :key="item"
                                :ref="item"
                                @click="clickGetNum"
                                @touchstart="handleTouchStart"
                                @touchmove="handleTouchMove"
                                @touchend="handleTouchEnd"
                        >{{item}}</li>
                </ul>
        </div>      
</template>
<script>
import axios from "axios"
export default{
        name : "alphabel",
        props : {
                cities : Object
        },
        computed : {
                numbers (){
                        //这个数组的目的是构建[  'A' , 'B' , 'C',············  ]
                        const numbers = []
                        for(let i in this.cities){
                                numbers.push(i)
                        }
                        return numbers
                }
        },
        data(){
                return {
                        // 只有当我们touchstart之后们才能触发touchmove，touchmove结束之后，关闭这个状态
                        // 所以，我们定义一个开关
                        touchStatus : false,
                        startY : 0,
                        timer : null//性能提升二，函数节流
                }
        },
        // 性能提升一，当页面执行完毕数据（获取完毕），我们用可以存储这个数据，
        updated(){
                this.startY = this.$refs['A'][0].offsetTop//A字母距离上边蓝色下沿的距离
        },
        methods : {
                clickGetNum(e){
                        this.$emit('getNum',e.target.innerText)
                        // console.log(e.target.innerText)
                },
                // 当在字母上下滑动的时候，我们让它自动对应左边的字母区域
                handleTouchStart(){
                        this.touchStatus = true//手指触摸的时候，true
                },
                handleTouchMove(e){
                        if(this.touchStatus){//我们只有在touchStatus为true的时候，才会move
                                if(this.timer){//函数节流，当 timer < 16 毫秒，清除timer，否则执行里边的语句
                                        clearTimeout(this.timer)
                                }
                                this.timer = setTimeout(()=>{
                                        const touchY = e.touches[0].clientY - 88//手指触摸的位置到屏幕顶端的距离（减去头部蓝色区域的高度就是手指触摸的位置到蓝色下沿的距离）
                                        // console.log(startY)
                                        // console.log(touchY)
                                        const index = Math.floor(( touchY - this.startY ) / 17) //算出手指触摸位置距离A字母的距离，然后除以一个字母的高度，即可算出当前手指触摸在哪个字母上的下标了
                                        // console.log(index)
                                        if(index >= 0 && index < this.numbers.length){
                                                this.$emit('getNum',this.numbers[index])
                                        }
                                },16)
                        }
                },
                handleTouchEnd(){
                        this.touchStatus = false//结束触摸的时候，我们让开关关闭
                }
        }
}
</script>
<style lang="stylus" scoped>
@import '../../../assets/styles/varibles.styl';

.list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 1.75rem;
        right: 0;
        bottom: 0;
        width: 0.4rem;
        font-size: bold;
        color: $bgColor;

        .item {
                line-height: 0.35rem;
        }
}
</style>



