<template>
<div>
        <div class="banner" @click="showImg">
                <img class="banner-image" :src="bannerImg" alt="">
                <div class="banner-info">
                        <div class="image-title">{{ sightName }}</div>
                        <div class="num">
                                <span class="iconfont">&#xe679;</span>
                                {{this.gallaryImgs.length}}
                        </div>
                </div>
        </div>
        <fade-animation>
                <commenGallary :getImg= "gallaryImgs"
                                 v-show= "myshowimg" 
                                 @close= "handleGallaryClose"
                                 >
                </commenGallary>
        </fade-animation>
</div>
</template>
<script>
import commenGallary from "commen/gallary/gallary";
import fadeAnimation from "commen/fade/fadeAnimation"
export default {
        name: "banner",
        props : {
                sightName : String,
                bannerImg : String,
                gallaryImgs : Array
        },
        data(){
                return {
                        myshowimg : false
        }
},
  components: {
    commenGallary,
    fadeAnimation
  },
  methods:{
          showImg(){
                  this.myshowimg = true
          },
          handleGallaryClose(){
                  this.myshowimg = false
          }
  }
};
</script>
<style lang="stylus" scoped>
.banner {
        overflow: hidden;
        height: 0;
        padding-bottom: 55%;
        position: relative;

        .banner-image {
                width: 100%;
        }

        .banner-info {
                display: flex;
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));

                .image-title {
                        margin-left: 0.3rem;
                        color: #fff;
                        line-height: 0.6rem;
                        font-size: 0.3rem;
                        flex: 1;
                }

                .num {
                        color: #ffffff;
                        height: 0.4rem;
                        line-height: 0.4rem;
                        padding: 0.01rem 0.4rem;
                        border-radius: 0.06rem;
                        background: rgba(0, 0, 0, 0.8);
                        margin-left: 0.3rem;
                }
        }
}
</style>



