<template>
        <div class="wrapper">
                <swiper :options="swiperOption" v-if="showSwiper">
                        <swiper-slide v-for="item of swiperList" :key="item.id">
                                <img class="swiper-img" :src="item.imgUrl">
                        </swiper-slide>
                        <div class="swiper-pagination"  slot="pagination"></div><!-- 下边小圆点 -->
                </swiper>
        </div>
</template>

<script>
export default {
  // 给这个组件起的名字是：myswiper
         name: "myswiper",
         props : {
                 swiperList : Array
         },
         data () {
                return {
                        // 下边小点
                        swiperOption : {
                                pagination : '.swiper-pagination',
                                loop : true ,//让我们这个轮播插件支持循环
                                autoplay : 1500,
                       },
                        // 存放图片的路径和id
                        // swiperList : [{
                        //         id : '0001',
                        //         imgUrl : "https://img1.qunarzz.com/vc/2f/8e/51/cb4d31b360c836f7d1bbe83c13.jpg"
                        // },{
                        //         id : '0002',
                        //         imgUrl : "https://img1.qunarzz.com/vc/a5/e0/64/c6bd8680fb67247db15df6057b.jpg"
                        // }]
                }
        },
        computed:{
                showSwiper () {
                        return this.swiperList.length
                }
        }
};
</script>

<style lang="stylus" scoped>
// 下边的这个意思是：样式进行穿透，只要是wrapper下边的子组件出现swiper-pagination-bullet-active这个样式，
// 那么我们会让它的背景颜色显示为白色，此方法不受scoped的限制
        >>> .swiper-pagination-bullet-active
                background : #fff !important
        .swiper-img
                width:100%

</style>


