<template>
        <div>
                <div class="hotRecommend">周末去哪</div>
                <ul>
                        <li class="wrapper" v-for="(item,index) of weekendList" :key="index">
                                <div class="img-box">
                                        <img class="pic" :src="item.imgUrl" alt="#">
                                </div>
                                <div class="content">
                                        <p class="content-title">{{ item.title }}</p>
                                        <p class="title-desc">{{ item.desc }}</p>
                                </div>
                        </li>
                </ul>
        </div>
</template>

<script>
export default {
  name: "myweekout",
  props : {
          weekendList : Array
  },
};
</script>

<style lang="stylus" scoped>
@import '../../../assets/styles/mixins.styl'
        .hotRecommend 
                width: 100%;
                background: #eeeeee;
                text-indent: 0.3rem;
                padding: 0.25rem 0;
                font-weight: bold;
                color: #666;
        .img-box
                width : 100%
                height : 0
                padding-bottom : 33.4%
                overflow : hidden 
                .pic 
                        width : 100%
        .content 
                padding: .14rem 0 .16rem .14rem 
                min-width : 0
                .content-title 
                        font-weight: bold
                        color: #000
                        margin-top: 0.02rem
                        ellipsis()
                .title-desc 
                        color: #666
                        padding: 0.12rem 0 0.15rem
                        ellipsis()
</style>


