<template>
        <div class="container" @click="handleGallaryClick">
                <div class="wrapper">
                        <swiper :options="swiperOptions">
                                <swiper-slide v-for="(item,index) of getImg" :key="index">
                                        <img :src= "item"  alt="" class="icon-img"/>
                                </swiper-slide>
                                <div class="swiper-pagination"  slot="pagination"></div><!--下边翻页数字-->
                        </swiper>
                </div>
        </div>
</template>
<script>
export default {
        name : 'commenGallary',
        props:{
                getImg: Array,
        },
        data(){
                return {
                      swiperOptions: {
                              pagination: ".swiper-pagination",
                              paginationType: "fraction",
                              observeParents: true,
                              observer: true
                      }  
                }
        },
        methods:{
                handleGallaryClick(){
                        this.$emit("close")
                }
        }
}        
</script>

<style lang="stylus" scoped>
        .container >>> .swiper-container
                overflow : inherit
        .container
                overflow : hidden
                position : fixed
                z-index : 99
                left :  0
                right : 0
                top : 0
                bottom : 0
                display : flex
                flex-direction : column
                justify-content : center
                background : #000
                .wrapper
                        width : 100%
                        height : 0
                        padding-bottom : 100%
                        .icon-img
                                width : 100%
                        .swiper-pagination
                                color : #fff
                                bottom : -1rem

</style>



