import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home/home'
import city from '@/pages/city/city'
import detail from '@/pages/detail/detail/'

Vue.use(Router)

export default new Router({
  routes: [{
	//   首页
	path: '/',
	name: 'home',
	component: home
      },{
	// 城市页
	path: '/city',
	name: 'city',
	component: city
    },{
	// 动态路由：前边的参数必须是detail/ 后边可以带一个id变量，这个变量就是对象的id
	path: '/detail/:id',
	name: 'detail',
	component: detail
    }],
    scrollBehavior (to, from, savedPosition) {
      return { x: 0, y: 0 }
    }
})



