<template>
        <div>
                <banner :sightName= "sightName" :bannerImg= "bannerImg" :gallaryImgs= "gallaryImgs"></banner>
                <detail-header></detail-header>
                <div class="content">
                        <detail-list :list= "categoryList "></detail-list>
                </div>
        </div>
</template>
<script>
import banner from './components/banner'
import detailHeader from './components/detail-header'
import detailList from './components/list'
import axios from 'axios'
export default{
        name: 'detail',
        components:{
                banner,
                detailHeader,
                detailList,
        },
        data(){
                return {
                        // list : [{
                        //         title:'大连',
                        //         children:[{
                        //                 title : '大连1111'
                        //         }]
                        // }],
                        sightName : '',
                        bannerImg : '',
                        gallaryImgs : [],
                        categoryList : []
                }
        },
        methods : {
                getDataInfo(){
                        // 获取动态路由中的id
                        // 方法一：
                        // axios.get('/api/detail.json?id=' + this.$route.params.id)
                        axios.get('https://www.easy-mock.com/mock/5c2c8c6c1d8d3d65c3bf4e86/example/detail',{
                                        params:{
                                                id : this.$route.params.id
                                        }
                                }
                        ).then(this.handleDataSucc)
                },
                handleDataSucc(res){
                        let myres = res.data
                        // console.log(myres.ret)
                        if(myres.ret && myres.data){
                                this.sightName = myres.data.sightName
                                this.bannerImg = myres.data.bannerImg
                                this.gallaryImgs = myres.data.gallaryImgs
                                this.categoryList = myres.data.categoryList
                        }
                }
        },
        // 通过keepalive进行缓存，所以mounted只执行一次
        // mounted () {
        //         this.getDataInfo()
        // },
        // 所以，我们在这再加一个activated钩子，当我们再次点击的时候，由于页面做了缓存机制（keep-alive），所以activated会执行
        activated(){
                this.getDataInfo()
        }
}        
</script>
<style lang="stylus" scoped>
        .content
                height : 50rem
</style>



