<template>
        <div>
                <div v-for="(item,index) of list" :key="index" class="item">
                        <div class="item-title">
                                <span class="item-title-icon"></span>
                                {{ item.title }}
                        </div>
                        <!-- 在这里我们写一个递归组件，这块判断item下边是否有子元素（意味着有二级或者多级标题） -->
                        <div v-if="item.children" class="two-item-list">
                                <!-- 当有子元素的时候，我们把子元素在传给list，也就是组件本身 -->
                                <detail-list :list= "item.children"></detail-list>
                        </div>
                </div>
        </div>

</template>

<script>

export default {
        // name的作用：使用递归组件的时候用的
        name: "detailList",
        props:{
                list: Array
        }
};
</script>
<style lang="stylus" scoped>
        .item-title
                line-height : .8rem
                font-size : .3rem
                padding : 0 .2rem
                border-bottom : .01rem #eee solid 
                .item-title-icon
                        background : url('http://s.qunarzz.com/piao/image/touch/sight/detail.png') no-repeat 0 -.86rem
                        display : inline-block
                        width : .4rem
                        height : .4rem
                        background-size : cover 
                        position : relative
                        top : .055rem
                        left : .03rem
        .two-item-list
                padding : 0 .4rem
                        
</style>


