<template>

        <div>
                <!-- 头部 -->
                <myheader></myheader>
                <!-- 轮播图 -->
                <myswiper :swiperList = "swiperList"></myswiper>
                <!-- 小图标 -->
                <myicon :iconList = "iconList"></myicon>
                <!-- 热销推荐 -->
                <myrecommend :recommendList = "recommendList"></myrecommend>
                <!-- 周末去哪 -->
                <myweekout :weekendList = "weekendList"></myweekout>
        </div>

</template>
<script>
// 这里home.vue是对应的最外层的容器组件，它会被拆分成很多很多容器组件，
// 由它帮助我们最后整合成一个大的页面，这些小的组件就可以放到components这个个文件中

// 引入局部的小组件，局部组件的用法是在下边是声明一个局部组件
import myheader from './components/header'
import myswiper from './components/swiper'
import myicon from './components/icons'
import myrecommend from './components/hot-recommend'
import myweekout from './components/weekout'
// 引入axios
import axios from 'axios'
import { mapState } from 'vuex'
export default{
        // 首页的名字
        name : "home",
        components:{
                myheader,
                myswiper,
                myicon,
                myrecommend,
                myweekout,
        },
        data(){
                return {
                        lastCity : "", 
                        swiperList : [],
                        iconList : [],
                        recommendList : [],
                        weekendList : []
                }
        },
        computed: {
                ...mapState(['city'])
        },
        methods : {
                getHomeInfo () {
                        axios.get('https://www.easy-mock.com/mock/5c2c8c6c1d8d3d65c3bf4e86/example/city-first-page?city=' + this.city)
                        .then(this.getInfoSecc)
                },
                getInfoSecc(res){
                        res = res.data
                        // 判断后端正确的返回了这个数据（ret在数据里边的值是true） 而且 有这个数据
                        if(res.ret && res.data){
                                this.swiperList = res.data.swiperList
                                this.iconList = res.data.iconList
                                this.recommendList = res.data.recommendList
                                this.weekendList = res.data.weekendList
                        }
                }
        },
        mounted () {
                this.lastCity = this.city //我们对上一个城市做一个保存,lastCity在data中
                //当页面初次挂在的时候，我们一定会发送一个ajax请求，我们来判断下，页面重新显示的时候，这个城市是否与上一次的一样，
                // 一样的话不发送ajax，不一样的话，就发送ajax
                this.getHomeInfo()
        },
        // 当页面重现显示的时候，activated会执行
        activated () {
                if ( this.lastCity !== this.city ){
                        this.lastCity = this.city
                        this.getHomeInfo()
                }
        }
}
</script>

<style>

</style>


