<template>
        <div class="myheader">
                <router-link to="/">
                        <div class="header-left">
                                <span class="iconfont">&#xe606;</span>
                        </div>
                </router-link>
                城市选择
        </div>

</template>

<script>
  export default{
          name : 'myheader',
  }      
</script>
<style lang="stylus" scoped>
        @import '../../../assets/styles/varibles.styl';
        .myheader
                position : relative
                height : .86rem
                line-height : .86rem
                text-align : center
                color : #fff
                background : $bgColor
                .header-left
                        position : absolute
                        left : .25rem
                        width: 0.64rem;
                        text-align: center;
                        color: #fff;
</style>



