<template>
        <div class="wrapperList" ref="wrapper">
                <div>
                        <div class="area">
                                <div class="list boder-topbottom">您的位置</div>
                                <div class="btn-list-wrapper">
                                        <div class="btnWrapper">
                                                <div class="btn">{{ this.currentCity }}</div>
                                        </div>
                                </div>
                        </div>
                        <div class="area">
                                <div class="list boder-topbottom">热门城市</div>
                                <div class="btn-list-wrapper">
                                        <div class="btnWrapper" v-for="item of gethotCity" :key="item.id"
                                        @click= "clickItem(item.name)">
                                                <div class="btn">{{ item.name }}</div>
                                        </div>
                                </div>
                        </div>
                        <div class="area" v-for="(item,key) of cities" :key="key" :ref="key">
                                <div class="list boder-topbottom">{{ key }}</div>
                                <div class="itemList" v-for="(innerItem,key) of item" :key="key.id" 
                                @click= "clickItem(innerItem.name)">
                                        <div class="item">{{ innerItem.name }}</div>
                                </div>
                        </div>
                </div>
        </div>      
</template>

<script>
import Bscroll from 'better-scroll'
import { mapState } from 'vuex'
export default {
  name: "cityList",
  props:{
          gethotCity : Array,
          cities : Object,
          mydata : String
  },
  computed: {
          ...mapState({
                // 我们把vue   city这个功用的数据，映射到这个组件的计算属性里，映射过来的名字叫做currentCity
                  currentCity: "city"
          })
  },
  mounted(){
// better-scroll规定用法
          this.scroll = new Bscroll(this.$refs.wrapper)
  },
  methods:{
          clickItem( city ){
                this.$store.dispatch('changeCity',city)
                // 点击跳转到首页
                this.$router.push('/')
          }
  },
//   监听mydata的变化
  watch:{
          mydata(){
                if( this.mydata ){
                        // 让better-scroll这个滚动区，自动的滚到某个元素上（better-scroll里边的方法）
                        // 比如外部传过来的A 或者 B ····滚到 A 或者B这个元素上
                        // 在li上定义一个ref=“key”，与接受过来的字母对应起来，
                        const element = this.$refs[this.mydata][0]
                        // console.log(element)
                        this.scroll.scrollToElement(element)
                }
          }
  }
};
</script>

<style lang="stylus" scoped>
@import '../../../assets/styles/varibles.styl';

.wrapperList {
        overflow: hidden;
        position: absolute;
        top: 1.8rem;
        left: 0;
        right: 0;
        bottom: 0;

        .list {
                line-height: 0.5rem;
                background: #eee;
                color: #666;
                padding-left: 0.1rem;
                border-bottom: #ccc solid 0.01rem;
        }

        .btn-list-wrapper {
                overflow: hidden;
                padding: 0.1rem 0.6rem 0.1rem 0.1rem;

                .btnWrapper {
                        float: left;
                        width: 33.33%;

                        .btn {
                                text-align: center;
                                border: 0.02rem solid #ccc;
                                padding: 0.05rem 0;
                                margin: 0.1rem;
                                border-radius: 0.06rem;
                        }
                }
        }

        .itemList {
                .item {
                        line-height: 0.7rem;
                        padding-left: 0.2rem;
                        border-bottom: 0.01rem #ccc solid;
                }
        }
}
</style>





