<template>
        <div>
                <router-link tag="div" to="/" class="clickBack"  v-show="!cityDetail">
                        <span class="iconfont">&#xe606;</span>
                </router-link>
                <div class="cityDetailHeader" v-show="cityDetail" :style= "myOpacity">
                        <router-link to="/" tag="div" class="header-left">
                                <span class="iconfont">&#xe606;</span>
                        </router-link>
                        城市详情
                </div>
        </div>
</template>
<script>
export  default {
        name : "detail-header",
        data () {
                return {
                        cityDetail : false,
                        myOpacity : {
                                opacity : 0
                        }
                }
        },
        // 当页面引入keep-alive的时候，页面第一次进入，
        // 钩子的触发顺序created-> mounted-> activated，退出时触发deactivated。
        // 当再次进入（前进或者后退）时，只触发activated。
        // mounted(){
        //         console.log(alert('2'))
        //         // window.addEventListener('scroll',this.handleSrcoll)
        // },
        // created(){
        //         console.log(alert('1'))
        //         // window.addEventListener('scroll',this.handleSrcoll)
        // },
        
        activated(){
                // console.log("3")
                window.addEventListener('scroll',this.handleSrcoll)
        },
        deactivated(){
                window.removeEventListener('scroll',this.handleSrcoll)
        },
        methods:{
                handleSrcoll(){
                        // console.log('scroll')
                        // 分析：获取滚动条滚动的距离，判断，距离大于50小于140的时候，我们让详情头部渐渐出现
                        let top = document.documentElement.scrollTop
                        if( top >50 ){
                                let opacity = top/140
                                opacity = opacity >1 ? 1 : opacity
                                this.myOpacity = {
                                        opacity 
                                }
                                this.cityDetail = true
                        }else{
                                this.cityDetail = false
                        }
                }
        }
}
        
</script>
<style lang="stylus" scoped>
 @import '../../../assets/styles/varibles.styl';
        .clickBack
                position : absolute 
                top : .3rem
                left : .3rem
                width : .8rem
                height : .8rem
                background : rgba(0 , 0 , 0 , .8)
                border-radius : .4rem
                text-align : center
                line-height : .8rem
                color : #fff
        .cityDetailHeader
                z-index : 1 
                position : fixed
                top : 0
                left : 0
                right : 0
                height : .86rem
                line-height : .86rem
                text-align : center
                color : #fff
                background : $bgColor
                .header-left
                        position : absolute
                        left : .25rem
                        width: 0.64rem;
                        text-align: center;
                        color: #fff; 

</style>



